<script>
  import * as mutations from 'store/user/mutation-types'
  import ChooseBox from 'components/choose-box.vue'

  function ajax (store, params) {
    return store.dispatch('FETCH_LIST_DATA', {
      ...store.state.user.base.paramsData,
      ...params
    })
  }

