<template>
  <el-dialog title="" v-model="show">
    <el-form>

      <el-form-item label="" :label-width="formLabelWidth" v-if="chooseList.indexOf('BM') >= 0">
        <el-select v-model="department" placeholder="">
          <el-option label="" value="shanghai"></el-option>
          <el-option label="" value="beijing"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" :label-width="formLabelWidth" v-if="chooseList.indexOf('YG_STATUS') >= 0">
        <el-checkbox v-model="YG_STATUS.status0" :true-label="0" :false-label="''"></el-checkbox>
        <el-checkbox v-model="YG_STATUS.status1" :true-label="1" :false-label="''"></el-checkbox>
        <el-checkbox v-model="YG_STATUS.status2" :true-label="2" :false-label="''"></el-checkbox>
        <el-checkbox v-model="YG_STATUS.status3" :true-label="3" :false-label="''"></el-checkbox>
      </el-form-item>


    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="confirm(false)"> </el-button>
      <el-button type="primary" @click="confirm"> </el-button>
    </div>
  </el-dialog>
</template>
<style>

</style>
<script>
  const LIST = {
    BM: 'BM', // 
    YG_STATUS: 'YG_STATUS', // 
    HT_STATUS: 'HT_STATUS', // 
    ZG_STATUS: 'ZG_STATUS', // 
    SB_TYPE: 'SB_TYPE'    // 
  }
  LIST
  export default {
    props: {
      chooseList: {
        type: Array,
        default: function () {
          return []
        }
      }
    },
    mounted () {
      console.log(this.chooseList)
      console.log(this.chooseList.indexOf('BM'))
    },
    data () {
      return {
        show: false,
        department: '', // 
        YG_STATUS: {
          status0: 0,
          status1: 1,
          status2: 2,
          status3: ''
        },
        formLabelWidth: '120px'
      }
    },
    methods: {
      showBox () {
        this.show = true
      },
      confirm (flag) {
        this.show = false
        if (flag === false) {
          return
        }
        const self = this
        this.$emit('confirm', {
          ...(self.chooseList.indexOf('BM') >= 0 ? {department: self.department} : {}),
          ...(self.chooseList.indexOf('YG_STATUS') >= 0 ? self.YG_STATUS : {})

        })
      }
    }
  }
</script>



